<template>
  <div>
    <div style="position: absolute; left: 50%; top: 70px; margin-left: -350px">
      <span style="position: absolute; left: 0px; width: 70px; top: 10px"></span>
      <el-steps simple align-center style="width: 600px; position: absolute; left: 70px;background-color: white" :active="active" finish-status="success">
        <el-step icon="el-icon-info" style="margin-left: 40px;" title=""></el-step>
        <el-step icon="el-icon-edit" title=""></el-step>
        <el-step icon="el-icon-upload" title=""></el-step>
      </el-steps>
    </div>
    <div v-show="firstStep" style="position: absolute; top: 100px;width: 1000px; margin-left: -500px;left: 50%">
      <div class="form_div">
        <div style="position: absolute; left: 32px; top: -10px;">
          <span style="position: absolute; left: 0px;padding-top: 5px; font-size: 14px;width: 70px; text-align: left"></span>
          <div id="manLabel" style="position: absolute; left: 68px;" class="labelType" @click="selectManLabel"></div>
          <div id="autoLabel" style="position: absolute; left: 178px;" class="labelType" @click="selectAutoLabel"></div>
        </div>
        <div v-show="isAutoLabel">
          <span style="position: absolute; left: 32px;top: 40px;font-size: 14px"></span>
          <el-input style="position: absolute; left: 100px;top: 40px;font-size: 14px;width: 390px" v-model="autoForm.missionName" ></el-input>
          <span style="position: absolute; left: 32px;top: 92px;font-size: 14px"></span>
          <el-input style="position: absolute; left: 100px;top: 92px;font-size: 14px;width: 390px" type="textarea" :rows="5" placeholder="" v-model="autoForm.contents"></el-input>
          <span style="position: absolute; left: 32px;top: 240px;font-size: 14px"></span>
          <el-select @change="labelChange2" style="position: absolute; left:100px;top: 240px;" v-model="autoForm.type" placeholder="">
            <div v-for="item in options" :key="item.value">
              <el-option :label="item.label" :value="item.value" :disabled="item.disabled"></el-option>
            </div>
          </el-select>
          <el-button style="position: absolute; left:350px;top: 233px;" @click="labelDetailsShow" class="label_info_button" icon="el-icon-info" type="text"></el-button>
          <el-button style="position: absolute; left:360px;top: 233px;" type="text"  v-show="isClassBtnShow" @click="handleClassBtn"></el-button>

        </div>
        <el-form v-show="!isAutoLabel" class="form" style="position:absolute;top: 50px" :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">
          <el-form-item label="" prop="topic" required>
            <el-input v-model="ruleForm.topic" ></el-input>
          </el-form-item>
          <el-form-item label="" prop="contents" required>
            <el-input type="textarea" :rows="2" placeholder="" v-model="ruleForm.contents"></el-input>
          </el-form-item>
          <el-form-item label="" required>
            <el-col :span="11">
              <el-form-item prop="dateStart" required>
                <el-date-picker value-format="yyyy-MM-dd" type="date" placeholder="" v-model="ruleForm.dateStart" style="width: 100%;"></el-date-picker>
              </el-form-item>
            </el-col>
            <el-col class="line" :span="2">-</el-col>
            <el-col :span="11">
              <el-form-item prop="dateEnd" required>
                <el-date-picker value-format="yyyy-MM-dd" type="date" placeholder="" v-model="ruleForm.dateEnd" style="width: 100%;"></el-date-picker>
              </el-form-item>
            </el-col>
          </el-form-item>
          <el-form-item label="" prop="type" required>
            <el-select @change="labelChange" style="position: absolute; left: 0px;" v-model="ruleForm.type" placeholder="">
              <div v-for="item in options" :key="item.value">
                <el-option :label="item.label" :value="item.value" :disabled="item.disabled"></el-option>
              </div>
            </el-select>
            <el-button @click="labelDetailsShow" class="label_info_button" icon="el-icon-info" type="text"></el-button>
          </el-form-item>
          <div style="position: absolute; top:220px; left: 360px;">
            <el-button type="text"  v-show="isClassBtnShow" @click="handleClassBtn"></el-button>
            <el-button type="text" v-show="isAttrBtnShow" @click="handleAttrBtn"></el-button>
          </div>
        </el-form>
        <router-view></router-view>
      </div>
    </div>

    <div v-show="secondStep" style="position: absolute; top: 100px;width: 1000px; margin-left: -500px;left: 50%">
      <span style="font-size: 13px; width: 400px; position: absolute; left: 250px; top: 30px; text-align: left"></span>
      <el-input-number size="mini" style="position: absolute; left: 315px; top: 25px;" v-model="ruleForm.workerNumber" :min="20" :max="100"></el-input-number>
      <span style="font-size: 13px; width: 400px; position: absolute; left: 460px; top: 30px; text-align: left"></span>
      <el-input-number size="mini" style="position: absolute; left: 520px; top: 25px;" v-model="ruleForm.workerPoints" :min="5" :max="20"></el-input-number>
      <span style="position:absolute; left:560px;top: 58px; font-size: 14px; color: #ffca06">{{ruleForm.workerPoints*ruleForm.workerNumber}}</span>
      <div>
        <span style="font-size: 13px; width: 400px; position: absolute; left: 250px; top: 70px; text-align: left"> <span style="font-size: 11px"> </span></span>
        <tag-selector style="position: absolute; left: 250px; top: 95px;"
                      v-bind:refresh-hot-tag="newHotTag" v-bind:candidate-label-list="hotTagList"
                      v-bind:selected-labels-list="selectedTagList" selected-div-width="440" selected-div-height="110">
        </tag-selector>
      </div>
    </div>

    <div v-show="thirdStep" style="position: absolute; top: 100px;width: 1000px; margin-left: -500px;left: 50%">
      <div style="position: absolute; left: 250px; top: 20px;" class="project_open">
        <div class="upload_wrap" @drop="drop($event)" @dragenter="dragenter($event)" @dragover="dragover($event)">
          <div style="position: absolute; left: 100px; top: 230px;"></div>
        </div>
        <div>
          <img style="position: absolute; left: 180px; top: 90px; width: 90px;cursor: pointer;" @click="fileClick"
               @drop="drop($event)" @dragenter="dragenter($event)" @dragover="dragover($event)"
               src="../assets/upload.png">
        </div>
        <div class="upload_wrap_text">
          {{imgList.length}}{{bytesToSize(this.size)}}
        </div>
        <el-button style="position: absolute; top:300px; left: 360px;" type="text" @click="imgClear()"></el-button>
        <input @change="fileChange($event)" type="file" id="upload_file" multiple style="display: none"/>
      </div>
    </div>

    <div style="position: absolute; top: 480px; width: 600px; left: 50%;margin-left: -330px">
      <button v-bind:disabled="isCancelBtnDisabled" @click="handleCancelButton" id="cancelBtn" class="stepBtn"></button>
      <button v-bind:disabled="isBeforeBtnDisabled"  @click="handleBeforeBtn" class="stepBtn"></button>
      <button v-bind:disabled="isNextBtnDisabled" @click="handleNextBtn" class="stepBtn"></button>
      <button v-bind:disabled="isSureBtnDisabled" @click="submitForm" id="sureBtn" class="stepBtn"></button>
    </div>


    <div style="position: absolute; top: 57px; left: 0px; width: 20%; height: 450px; border-bottom-left-radius: 1px; border-color: grey" >
      <div style="position: absolute; left: 50%; margin-left: -29px; height: 50px;">
        
      </div>
      <happy-scroll class="happy_scroll" color="rgba(51,51,51,0.2)" hide-vertical="false">
        <div v-for="(item,index) of imgList" :key="item">
          <div class="upload_warp_img_div_top">
            <div class="upload_warp_img_div_text">
              {{item.file.name}}
            </div>
            <img src="../assets/del.png" class="upload_warp_img_div_del" @click="fileDel(index)">
          </div>
          <img style="width: 100%;" :src="item.file.src">
        </div>
      </happy-scroll>
    </div>
    <el-dialog title="" :visible.sync="classDialogVisible" width="600px">
      <el-form style="height: 220px; ">
        <div v-for="item in classArray" :key="item" >
          <el-input style="width: 120px; float:left; margin: 5px" v-model="item.value"></el-input>
        </div>
        <el-button @click="addClass" type="text" icon="el-icon-circle-plus-outline" style="float: left; font-size: 24px" size="medium"></el-button>
        <el-button @click="classDialog_sureBtn" type="primary" style="font-size: 12px;position: absolute; right: 40px; bottom: 10px;"></el-button>
        <span style="color: red; position: absolute;left: 30px; bottom: 10px">10</span>
      </el-form>
    </el-dialog>
    <el-dialog title="" :visible.sync="attrDialogVisible" width="600px">
      <el-form style="height: 220px; ">
        <div v-for="item in attrArray" :key="item" >
          <el-input style="width: 120px; float:left; margin: 5px" v-model="item.value"></el-input>
        </div>
        <el-button @click="addAttribute" type="text" icon="el-icon-circle-plus-outline" style="float: left; font-size: 24px" size="medium"></el-button>
        <el-button @click="attrDialog_sureBtn" type="primary" style="font-size: 12px;position: absolute; right: 40px; bottom: 10px;"></el-button>
        <span  style="color: red; position: absolute;left: 30px; bottom: 10px">10</span>
      </el-form>
    </el-dialog>
    <div v-show="isLabelDetailsShow" style="position: absolute; right: 10px; top: 70px; width: 300px; height: 340px; color: grey;">
      <span style="font-size: 20px;position:absolute; top: 5px; left: 110px;"></span>
      <span style="font-size: 16px; position: absolute; top: 40px; left: 12px; color: #4CAF50"></span>
      <p style="font-size: 14px; position: absolute; top: 50px; left: 12px; text-align: left"></p>
      <span style="font-size: 16px; position: absolute; top: 105px; left: 12px;color: #4CAF50"></span>
      <p style="font-size: 14px; position: absolute; top: 115px; left: 12px; text-align: left"></p>
      <span style="font-size: 16px; position: absolute; top: 150px; left: 12px;color: #4CAF50"></span>
      <p style="font-size: 14px; position: absolute; top: 160px; left: 12px; text-align: left"></p>
      <span style="font-size: 16px; position: absolute; top: 215px; left: 12px;color: #4CAF50"></span>
      <p style="font-size: 14px; position: absolute; top: 225px; left: 12px; text-align: left"></p>
      <span style="font-size: 16px; position: absolute; top: 260px; left: 12px;color: #4CAF50"></span>
      <p style="font-size: 14px; position: absolute; top: 270px; left: 12px; text-align: left"></p>
    </div>

    <div v-show="doAutoLabel">
      <div class="modal" style="display: block;">  </div>
      <my-loading style="position: absolute;left: 50%; margin-left: -82px;top: 180px;" type="rainbow"></my-loading>
      <span style="position: absolute;left: 50%; margin-left: -60px;top: 310px;font-size: 18px" ></span>
      <span style="position: absolute;left: 50%; margin-left: -75px;top: 335px;font-size: 12px;color: #747474" ></span>
      <el-button type="primary" size="small" style="position: absolute;left: 50%;margin-left: -35px;top: 358px;font-size: 16px" @click="backStageRun"></el-button>
    </div>

  </div>
</template>

<script>
  import JSZip from 'jszip'
  import TagSelector from './tagSelector/tagSelector'
  import MyLoading from './myLoading/myLoading'
  export default {
    components: {TagSelector, MyLoading},
    name: 'NewProject',
    watch: {
      active(){
        if(this.active==0){
          this.firstStep=true;
          this.secondStep =false;
          this.thirdStep = false;
        }
        if(this.active==1){
          this.firstStep=false;
          this.secondStep =true;
          this.thirdStep = false;
        }
        if(this.active==2){
          this.firstStep=false;
          this.secondStep =false;
          this.thirdStep = true;
        }
      }
    },
    data () {
      var validateTopic = (rule, value, callback) => {
        var len = 0;
        for (var i=0; i<value.length; i++) {
          if (value.charCodeAt(i)>127 || value.charCodeAt(i)==94) {
            len += 2;
          } else {
            len ++;
          }
        }
        if(len>20){
          callback(new Error('1020'))
        }
        if(len==0){
          callback(new Error(''))
        }
      }
      return {
        autoForm:{
          type: '',
          contents: '',
          missionName: '',
          points: 10,
        },
        doAutoLabel:false,
        isAutoLabel:false,
        hotTagList:['dog', 'cat', 'mouse', 'horse', 'caption', 'detection', 'animal', 'car',
          'fruit', 'vegetable'],
        selectedTagList:[],
        firstStep:true,
        secondStep:false,
        thirdStep:false,
        isCancelBtnDisabled: false,
        isNextBtnDisabled: false,
        isBeforeBtnDisabled: true,
        isSureBtnDisabled: true,

        active:0,
        rules: {
          topic: [
            {validator: validateTopic, trigger: 'blur'}
          ],
        },
        isLabelDetailsShow: false,
        isClassBtnShow: false,
        isAttrBtnShow: false,
        isWarnShow: false,
        classDialogVisible: false,
        attrDialogVisible: false,
        classNumber: 4,
        classValue: '',
        selectsArray: [],
        classArray: [{
          value: ''
        }],
        attrNumber: 4,
        attrValue: '',
        attrArray: [{
          value: ''
        }],
        imgList: [],
        imgFileList: [],
        size: 0,
        ruleForm: {
          topic: '',
          contents: '',
          type: '',
          dateStart: '',
          dateEnd: '',
          workerNumber: 20,
          workerPoints: 5
        },
        options: [
          {
            value: 'Caption',
            label: '',
            info: '',

          },
          {
            value: 'Detection',
            label: '',
            info: '',
          },
          {
            value: 'Segmentation',
            label: '',
            info: '',

          },
          {
            value: 'Classification',
            label: '',
            info: '',

          },
          {
            value: 'Attribute',
            label: '',
            info: ' ',
          }
        ],
        value: ''
      }
    },
    created () {
      for (var i = 1; i < this.classNumber; i++) {
        var m = {
          value: this.classValue
        }
        this.classArray.push(m)
      }
      for (var i = 1; i < this.attrNumber; i++) {
        var m = {
          value: this.attrValue
        }
        this.attrArray.push(m)
      }
    },
    methods: {
      backStageRun(){
        this.doAutoLabel=false;
        this.goMyProject()
      },
      selectManLabel(){
        for(var i=0;i<this.options.length;i++){
          this.options[i].disabled=false;
        }
        this.isAutoLabel=false;
        document.getElementById('manLabel').style.backgroundColor = '#1d86ff'
        document.getElementById('manLabel').style.color = 'white'
        document.getElementById('manLabel').style.border='0'
        document.getElementById('autoLabel').style.backgroundColor = 'white'
        document.getElementById('autoLabel').style.color = 'black'
        document.getElementById('autoLabel').style.border='1px solid #999'
      },
      selectAutoLabel(){
        this.isAutoLabel=true;
        this.options[2].disabled=true;
        this.options[4].disabled=true;
        document.getElementById('autoLabel').style.backgroundColor = '#1d86ff'
        document.getElementById('autoLabel').style.color = 'white'
        document.getElementById('autoLabel').style.border='0'
        document.getElementById('manLabel').style.backgroundColor = 'white'
        document.getElementById('manLabel').style.color = 'black'
        document.getElementById('manLabel').style.border='1px solid #999'
      },
      handleBeforeBtn(){
        this.isNextBtnDisabled=false;
        if(this.active>0){
          this.active--;
        }
        if(this.isAutoLabel==true)
          this.active=0
        if(this.active==0){
          this.isBeforeBtnDisabled=true
        }
      },
      newHotTag () {
        var preList = ['dog', 'cat', 'mouse', 'horse', 'caption', 'detection', 'animal', 'car',
          'fruit', 'vegetable', 'phone', 'medicine','women','star','actor','actress','Asian','cartoon','comic',
          '','','','','','','computer','','','apple','bird']
        this.hotTagList=[]
        while(true){
          if(this.hotTagList.length<11){
            var index = this.random_Num(0,preList.length);
            var test = true;
            for(var j=0;j<this.hotTagList.length;j++){
              if(preList[index]==this.hotTagList[j]){
                test = false;
              }
            }
            if(test == true){
              this.hotTagList.push(preList[index])
            }
          }else{
            return;
          }
        }
        //this.hotTagList.push('new hot tag')
      },
      handleNextBtn(){
        this.isBeforeBtnDisabled=false;

        if(this.active<3){
          this.active++;
        }
        if(this.isAutoLabel==true)
          this.active=2;
        if(this.active==2){
          this.isSureBtnDisabled=false;
          this.isNextBtnDisabled=true
        }
      },
      labelDetailsShow () {
        this.isLabelDetailsShow = !this.isLabelDetailsShow

      },
      handleClassBtn () {
        this.classDialogVisible = true
      },
      handleAttrBtn () {
        this.attrDialogVisible = true
      },
      labelChange () {
        if (this.ruleForm.type == 'Classification') {
          this.isClassBtnShow = true
          this.isAttrBtnShow = false
          this.classDialogVisible = true
        } else if (this.ruleForm.type == 'Attribute') {
          this.isClassBtnShow = false
          this.isAttrBtnShow = true
          this.attrDialogVisible = true
        } else {
          this.isClassBtnShow = false
          this.isAttrBtnShow = false
          this.classDialogVisible = false
          this.attrDialogVisible = false
        }
      },
      labelChange2 () {
        if (this.autoForm.type == 'Classification') {
          this.isClassBtnShow = true
          this.isAttrBtnShow = false
          this.classDialogVisible = true
        } else {
          this.isClassBtnShow = false
          this.isAttrBtnShow = false
          this.classDialogVisible = false
          this.attrDialogVisible = false
        }
      },
      addClass () {
        if (this.classNumber < 10) {
          this.classNumber++
          this.classArray.push({value: ''})
        } else {
          this.isWarnShow = true
        }
      },
      addAttribute () {
        if (this.attrNumber < 10) {
          this.attrNumber++
          this.attrArray.push({value: ''})
        } else {
          this.isWarnShow = true
        }
      },
      attrDialog_sureBtn () {
        this.attrDialogVisible = false
      },
      classDialog_sureBtn () {
        this.classDialogVisible = false
      },

      openSucc (text) {
        this.$notify({
          title: '',
          message: text,
          type: 'success',
          duration: 2000,
          position: 'top-left'
        })
      },
      openInfo (text) {
        this.$notify({
          title: '',
          message: text,
          type: 'info',
          duration: 2000,
          position: 'top-left'
        })
      },
      openWarn (text) {
        this.$notify({
          title: '',
          message: text,
          type: 'warning',
          duration: 2000,
          position: 'top-left'
        })
      },
      LTrim (str) {
        return str.replace(/^\s*/g, '')
      },
      // 
      RTrim (str) {
        return str.replace(/\s*$/g, '')
      },
      handleCancelButton () {
        var path = '/' + localStorage.getItem('username')
        this.$router.push({path: path + '/requester'})
      },
      goMyProject () {
        var path = '/' + localStorage.getItem('username')
        this.$router.push({path: path + '/requester'})
        window.location.reload()
      },
      submitForm(){
        if(this.isAutoLabel){
          this.submitAutoForm()
        }else{
          this.submitManForm()
        }
      },

      submitAutoForm(){
        if (this.imgFileList.length == 0) {
          this.openWarn('')
          return;
        }
        if (this.autoForm.type == 'Classification') {
          for (var i = 0; i < this.classArray.length; i++) {
            if (this.LTrim(this.RTrim(this.classArray[i].value)) != '') {
              this.selectsArray.push(this.LTrim(this.RTrim(this.classArray[i].value)))
            }
          }
        }
        var xmlhttp = new XMLHttpRequest()
        var _this = this
        var username = localStorage.getItem('username')
        var missionID = 0
        var type = _this.autoForm.type;
        xmlhttp.onreadystatechange = function () {
          if (xmlhttp.readyState == 4 && xmlhttp.status == 200) {
            console.log('missionID'+xmlhttp.responseText)
            missionID = xmlhttp.responseText
            if(type=='Detection'){
              _this.uploadAutoDectionImg(missionID)
            }else if(type=='Caption'){
              _this.uploadAutoCaptionImg(missionID)
            }else if(type == 'Classification'){
              _this.uploadAutoClassificationImg(missionID)
            }
            _this.openSucc('Submit successfully')
            _this.goMyProject()
          }
        }
        var autoMission = {
          requestorID: username,
          missionName: _this.autoForm.missionName,
          description: _this.autoForm.contents,
          type: _this.autoForm.type,
          points: _this.autoForm.points,
          types: [],
          size: _this.imgFileList.length
        }
        if (autoMission.type == 'Classification') {
          autoMission.types =  _this.selectsArray
        }
       // console.log(this.selectsArray)
        xmlhttp.open('POST', 'http://localhost:8080/counts/mission/addAutoMission', true)
        xmlhttp.setRequestHeader('Content-type', 'application/json; charset=utf-8')
       // console.log(JSON.stringify(autoMission))
        xmlhttp.send(JSON.stringify(autoMission));
      },

      submitManForm () {
        if (this.ruleForm.type == 'Classification') {
          for (var i = 0; i < this.classArray.length; i++) {
            if (this.LTrim(this.RTrim(this.classArray[i].value)) != '') {
              this.selectsArray.push(this.LTrim(this.RTrim(this.classArray[i].value)))
            }
          }
        } else if (this.ruleForm.type == 'Attribute') {
          for (var i = 0; i < this.attrArray.length; i++) {
            if (this.LTrim(this.RTrim(this.attrArray[i].value)) != '') {
              this.selectsArray.push(this.LTrim(this.RTrim(this.attrArray[i].value)))
            }
          }
        }
        var isValid = true

        if (this.imgFileList.length == 0) {
          this.openWarn('')
          isValid = false
        }
        if (isValid == true) {
          var xmlhttp = new XMLHttpRequest()
          var _this = this
          var username = localStorage.getItem('username')
          var missionID = 0
          xmlhttp.onreadystatechange = function () {
            if (xmlhttp.readyState == 4 && xmlhttp.status == 200) {
              missionID = xmlhttp.responseText
              _this.uploadImg(missionID)
              _this.openSucc('Submit successfully')
              _this.goMyProject()
            }
          }
          let formData = new FormData()
          var mission = {
            requestorID: username,
            missionName: _this.ruleForm.topic,
            description: _this.ruleForm.contents,
            begin: _this.ruleForm.dateStart,
            end: _this.ruleForm.dateEnd,
            type: _this.ruleForm.type,
            points: _this.ruleForm.workerPoints,
            maxNumber: _this.ruleForm.workerNumber,
            selects: [],
            tags: _this.selectedTagList,
            sum:_this.imgFileList.length
          }
          if (mission.type == 'Classification' || mission.type == 'Attribute') {
            mission.selects =  _this.selectsArray
          }
          xmlhttp.open('POST', 'http://localhost:8080/counts/image/addmission', true)
          xmlhttp.setRequestHeader('Content-type', 'application/json; charset=utf-8')
          xmlhttp.send(JSON.stringify(mission))
        }
      },

      uploadAutoCaptionImg(missionID){
        var xmlhttp = new XMLHttpRequest()
        var _this = this
        if (missionID != 0) {
          xmlhttp.onreadystatechange = function () {
            if (xmlhttp.readyState == 4 && xmlhttp.status == 200) {
              console.log(missionID)
            }
          }
          let formData = new FormData()
          var str = '' + missionID
          formData.append('missionID', str)
          for (var i = 0; i < this.imgFileList.length; i++) {
            formData.append('file', _this.imgFileList[i])
          }
          console.log('imglist'+this.imgFileList)
          xmlhttp.open('POST', 'http://localhost:8080/counts/image/uploadAutoCaptionImg', true)
          xmlhttp.send(formData)
        }
      },
      uploadAutoClassificationImg(missionID){
        var xmlhttp = new XMLHttpRequest()
        var _this = this
        if (missionID != 0) {
          xmlhttp.onreadystatechange = function () {
            if (xmlhttp.readyState == 4 && xmlhttp.status == 200) {
              console.log(missionID)
            }
          }
          let formData = new FormData()
          var str = '' + missionID
          formData.append('missionID', str)
          for (var i = 0; i < this.imgFileList.length; i++) {
            formData.append('file', _this.imgFileList[i])
          }
          xmlhttp.open('POST', 'http://localhost:8080/counts/image/uploadAutoClassificationImg', true)
          xmlhttp.send(formData)
        }
      },
      uploadAutoDectionImg(missionID){
        var xmlhttp = new XMLHttpRequest()
        var _this = this
        if (missionID != 0) {
          xmlhttp.onreadystatechange = function () {
            if (xmlhttp.readyState == 4 && xmlhttp.status == 200) {
              console.log(missionID)
            }
          }
          let formData = new FormData()
          var str = '' + missionID
          formData.append('missionID', str)
          for (var i = 0; i < this.imgFileList.length; i++) {
            formData.append('file', _this.imgFileList[i])
          }
          //console.log("uploadSetection")
          xmlhttp.open('POST', 'http://localhost:8080/counts/image/uploadAutoDetectionImg', false)
          xmlhttp.send(formData)
        }
      },
      uploadImg (missionID) {
        var xmlhttp = new XMLHttpRequest()
        var _this = this
        if (missionID != 0) {
          xmlhttp.onreadystatechange = function () {
            if (xmlhttp.readyState == 4 && xmlhttp.status == 200) {
              console.log(missionID)
            }
          }
          let formData = new FormData()
          var str = '' + missionID
          formData.append('mission', str)
          for (var i = 0; i < this.imgFileList.length; i++) {
            formData.append('file', _this.imgFileList[i])
          }
          xmlhttp.open('POST', 'http://localhost:8080/counts/image/uploadImg', true)
          xmlhttp.send(formData)
        }
      },

      resetForm (formName) {
        console.log(this.ruleForm.dateStart)
        this.ruleForm.workerNumber = 20
        this.ruleForm.workerPoints = 5
        this.isClassBtnShow = false
        this.isAttrBtnShow = false
        this.$refs[formName].resetFields()
        this.imgList = []
        this.imgFileList = []
        this.size = 0
        this.openSucc('')
      },

      imgClear () {
        if (this.imgList.length == 0) {
          this.openInfo('')
          return
        }
        this.$confirm('', 'Confirm', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.imgList = []
          this.imgFileList = []
          this.size = 0
          this.openSucc('')
        })
      },
      fileClick () {
        document.getElementById('upload_file').click()
      },
      fileChange (el) {
        if (!el.target.files[0].size) return
        this.fileList(el.target)
        el.target.value = ''
      },
      fileList (fileList) {
        let files = fileList.files
        for (let i = 0; i < files.length; i++) {
          // 
          if (files[i].type != '') {
            this.fileAdd(files[i])
          } else {
            // 
            this.folders(fileList.items[i])
          }
        }
      },
      folders (files) {
        let _this = this
        // file
        if (files.kind) {
          files = files.webkitGetAsEntry()
        }
        files.createReader().readEntries(function (file) {
          for (let i = 0; i < file.length; i++) {
            if (file[i].isFile) {
              _this.foldersAdd(file[i])
            } else {
              _this.folders(file[i])
            }
          }
        })
      },
      foldersAdd (entry) {
        let _this = this
        entry.file(function (file) {
          _this.fileAdd(file)
        })
      },
      fileDel (index) {
        this.$confirm('', 'Confirm', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.size = this.size - this.imgList[index].file.size// 
          this.imgList.splice(index, 1)
          this.imgFileList.splice(index, 1)
          this.openSucc('')
        })
      },
      fileAdd (file) {
        if (this.limit !== undefined) this.limit--
        if (this.limit !== undefined && this.limit < 0) return
        // 
        var _this = this;
        console.log('file '+file.type)
        if (file.type.indexOf('image')==-1) {
          //
          //console.log('name '+file)
          if(file.type.indexOf('x-zip-compressed')!=-1){
            var zip = new JSZip()
            zip.loadAsync(file).then(function (file) {
              var files = file.files;
              console.log(files)
              var imgArr = []
              var image = {};
              for(var i in files){
                console.log(i)
                imgArr.push(i)
              }
              for(var j =0;j<imgArr.length;j++){
                var fileName = imgArr[j]
                var buffer = zip.file(imgArr[j]).async("arraybuffer")
                var str = _this._arrayBufferToBase64(buffer)
                var pIndex = fileName.indexOf('.');
                var type = fileName.substr(pIndex + 1);
                var re = 'data:image/' + type + ';base64,';
                image[fileName] = re+str;
              }
              console.log(image)
            })
          }
        } else {
          this.size = this.size + file.size
          this.imgFileList.push(file)
          let reader = new FileReader()
          let image = new Image()
          let _this = this
          reader.readAsDataURL(file)
          reader.onload = function () {
            file.src = this.result
            image.onload = function () {
              let width = image.width
              let height = image.height
              file.width = width
              file.height = height
              _this.imgList.push({
                file
              })
            }
            image.src = file.src
          }
        }
      },
      _arrayBufferToBase64(buffer) {
        var binary = '';
        var bytes = new Uint8Array(buffer);
        var len = bytes.byteLength;
        for (var i = 0; i < len; i++) {
          binary += String.fromCharCode(bytes[i]);
        }
        return window.btoa(binary);
      },
      bytesToSize (bytes) {
        if (bytes === 0) return '0 B'
        let k = 1000, // or 1024
          sizes = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'],
          i = Math.floor(Math.log(bytes) / Math.log(k))
        return (bytes / Math.pow(k, i)).toPrecision(3) + ' ' + sizes[i]
      },
      dragenter (el) {
        el.stopPropagation()
        el.preventDefault()
      },
      dragover (el) {
        el.stopPropagation()
        el.preventDefault()
      },
      drop (el) {
        el.stopPropagation()
        el.preventDefault()
        this.fileList(el.dataTransfer)
      }
    }
  }
</script>

<style scoped>
  .label_info_button {
    color: #9c9c9c;
    position: absolute;
    left: 240px;
    top: 0px;
  }
  .label_info_button:hover{
    color:grey;
  }
  .form_div{
    top: 40px;
    position: absolute;
    width: 700px;
    left: 50%;
    margin-left: -300px;
  }
  .form{
    position: absolute;
    width: 500px;
  }
  .upload_wrap{
    position: absolute;
    top: 0px;
    width: 450px;
    left: 0px;
    height: 300px;
    border: 1px solid #a8a8a8;
    color: #999;
  }
  .upload_wrap_text {
    width: 400px;
    text-align: left;
    position: absolute;
    left:5px;
    top:310px;
    font-size: 14px;
  }

  .upload_warp_left {

    cursor: pointer;
  }
  .upload_warp_img_div_del {
    width: 18px;
    float: left;
  }

  .happy_scroll{
    position: absolute;
    top: 30px;
    left: 0px;
    width:100%;
  }
  .upload_warp_img_div_top {
    top: 0;
    width: 100%;
    height: 30px;
    background-color: rgba(101, 83, 108, 0.4);
    line-height: 28px;
    text-align: left;
    color: #fff;
    font-size: 12px;
    text-indent: 10px;
  }
  .upload_warp_img_div_text {
    white-space: nowrap;
    width: 92%;
    overflow: hidden;
    text-overflow: ellipsis;
    float: left;
  }
  .stepBtn{
    border-radius: 3px;
    height: 25px;
    width: 110px;
    background-color: white;
    border: 1px solid #999;
  }
  .stepBtn:hover{
    background-color: #2ea2ff;
    border: 0px;
    color: white;
  }
  #sureBtn:hover{
    background-color: #4CAF50;
    border: 0px;
    color: white;
  }
  #cancelBtn:hover{
    background-color: #ff8432;
    border: 0px;
    color: white;
  }
  .stepBtn[disabled]{
    background-color: #adadad;
    color: white;
    cursor: pointer;
  }
  #cancelBtn[disabled]{
    background-color: #adadad;
    color: white;
    cursor: pointer;
  }
  #sureBtn[disabled]{
    background-color: #adadad;
    color: white;
    cursor: pointer;
  }
  .labelType{
    width:100px;
    padding-top: 8px;
    height: 28px;
    border-radius: 3px;
    border: 1px solid #999;
    font-size: 13px;
    cursor: pointer;
  }
  .labelType:hover{
    border: 0px;
    color: white;
    background-color: #2ea2ff;
    cursor: pointer;
  }
  .modal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: white;
    filter:alpha(opacity:80);
    opacity:0.8;
    -moz-opacity:0.8;
  }
</style>



